/*------------------------------------*\
  #FLYOUT BLOCK
\*------------------------------------*/
[data-flyout].is-activated {
  z-index: 5000;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transform: translateX(-100vw); }

[data-flyout].is-activated.is-open {
  transform: translateX(0); }

[data-flyout-content] {
  position: relative;
  height: 100%;
  background-color: #fff; }

[data-flyout].is-activated [data-flyout-content] {
  overflow-x: hidden;
  overflow-y: auto; }

body.has-flyout--open {
  overflow: hidden; }

.flyout-overlay {
  display: none; }

.has-flyout .flyout-overlay {
  z-index: 1499;
  display: block;
  will-change: background-color;
  background-color: transparent;
  transition: background-color linear 0.15s; }

.has-flyout--open .flyout-overlay {
  display: block;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.75); }

/*------------------------------------*\
  #FLYOUT BLOCK -- MODAL
\*------------------------------------*/
[data-flyout][data-flyout-modal].is-activated [data-flyout-content] {
  position: relative; }
  @media (min-width: 750px) {
    [data-flyout][data-flyout-modal].is-activated [data-flyout-content] {
      margin: 10vh auto;
      max-height: 80vh;
      max-width: 80%;
      height: auto; } }
  @media (min-width: 1000px) {
    [data-flyout][data-flyout-modal].is-activated [data-flyout-content] {
      max-width: 60%; } }

/*------------------------------------*\
  #ANIMATIONS
\*------------------------------------*/
.slide-in-up {
  animation: slide-in-up 0.3s ease; }

@keyframes slide-in-up {
  from {
    transform: translate3d(0, 100%, 0);
    visibility: visible; }
  to {
    transform: translate3d(0, 0, 0); } }

.slide-in-down {
  animation: slide-in-down 0.3s ease; }

@keyframes slide-in-down {
  from {
    transform: translate3d(0, -100%, 0);
    visibility: visible; }
  to {
    transform: translate3d(0, 0, 0); } }

.slide-in-left {
  animation: slide-in-left cubic-bezier(0.25, 0.46, 0.45, 0.94) 0.15s; }

@keyframes slide-in-left {
  from {
    -webkit-transform: translate3d(-100%, 0, 0);
    transform: translate3d(-100%, 0, 0); }
  to {
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0); } }

.slide-in-right {
  animation: slide-in-right cubic-bezier(0.25, 0.46, 0.45, 0.94) 0.15s; }

@keyframes slide-in-right {
  from {
    -webkit-transform: translate3d(100%, 0, 0);
    transform: translate3d(100%, 0, 0); }
  to {
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0); } }

.slide-out-up {
  animation: slide-out-up 0.3s ease; }

@keyframes slide-out-up {
  from {
    transform: translate3d(0, 0, 0); }
  to {
    visibility: hidden;
    transform: translate3d(0, -100%, 0); } }

.slide-out-down {
